<template>
  <el-container class="moduleWrapper">
    <el-header class="header" height="60px">
      <div class="title" @click="goHome">
        <div class="huiZhang"></div>
        <div>南泥湾采油厂</div>
      </div>
      <el-menu
        :default-active="topIndex"
        class="topMenu"
        mode="horizontal"
        text-color="#fff"
        background-color="#CF1F00"
        active-text-color="#ffd04b"
        router
      >
        <el-menu-item
          v-for="(menu, index) in menus"
          :key="index"
          :index="menu.menuHref"
        >
          <span slot="title">{{ menu.menuName }}</span>
        </el-menu-item>
        <el-menu-item index="/setting">
          <span slot="title">系统设置</span>
        </el-menu-item>
        <el-submenu index="/person" class="personal-center">
          <template slot="title">
            <span class="nickName">{{ loginInfo.loginName }}</span>
          </template>
          <el-menu-item @click="updateUserPassword">修改密码</el-menu-item>
          <el-menu-item @click="exit">退出登录</el-menu-item>
        </el-submenu>
      </el-menu>
    </el-header>
    <el-main class="main">
      <router-view></router-view>
    </el-main>
    <el-dialog
      title="修改密码"
      class="passWordDialog"
      :visible.sync="updatePasswordVisible"
      :before-close="cancel"
    >
      <el-form
        label-position="right"
        label-width="90px"
        :model="updatePasswordFormData"
        size="small"
      >
        <el-form-item label="当前密码">
          <el-input
            type="password"
            v-model="updatePasswordFormData.oldPassword"
            placeholder="请输入当前密码"
          ></el-input>
        </el-form-item>
        <el-form-item label="新密码">
          <el-input
            type="password"
            v-model="updatePasswordFormData.newPassword"
            placeholder="请输入新密码"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel" size="small">取 消</el-button>
        <el-button type="primary" @click="saveNewPwd" size="small"
          >提 交</el-button
        >
      </div>
    </el-dialog>
  </el-container>
</template>

<script>
import Sidebar from "@/components/common/AppNav";
import tabNav from "@/components/common/tabNav";
import AppUpload from "@/components/AppAvatarUpload";
import {
  findUserPermissionTree,
  getPersonInfo,
  changePwd,
  loginOut,
} from "@/http/AppApi";
import { clearLocalStrorage, getToken } from "@/utils/LocalDataUtil";
import { clearSessionStorage } from "@/utils/SessionDataUtil";
import conf from "@/config/index";
export default {
  name: "app",
  data() {
    return {
      topIndex: this.$route.name,
      isTabShow: false,
      needTagsView: true,
      isCollapse: true,
      updatePasswordVisible: false,
      menus: [],
      menuList: [],
      allRole: [],
      cardOption: [],
      deptOption: [],
      genderOption: [],
      nationOption: [],
      educationOption: [],
      politicalStatusOption: [],
      loginInfo: {},
      myInfo: {},
      updatePasswordFormData: {},
    };
  },
  mounted() {
    this.getModules();
    this.getPersonInfos();
  },
  computed: {},
  methods: {
    //获取登录信息
    getPersonInfos() {
      getPersonInfo({}).then((resp) => {
        let data = resp.value;
        if (data && data.code == "0000") {
          this.loginInfo = data.data;
        }
      });
    },
    getModules() {
      let m = this;
      let params = {
        menuId: "",
        menuName: "",
        sysCode: "",
      };
      //头部菜单获取
      findUserPermissionTree(params).then((resp) => {
        if (resp.value && resp.value.code == "0000") {
          let data = resp.value.data;
          let treeData = [];
          for (var x in data) {
            if (data[x].parentId === 0) {
              treeData.push(data[x]);
            }
          }
          m.menus = treeData;
        }
      });
    },
    //点击logo
    goHome() {
      this.$router.push({ path: "/bigdata" });
    },
    //退出操作
    exit() {
      loginOut().then((resp) => {
        if (resp && resp.code == "1100") {
          clearLocalStrorage();
          this.$router.push({ name: "login" });
        } else {
          this.$message.error(resp.message);
        }
      });
    },
    //修改密码
    updateUserPassword() {
      this.updatePasswordVisible = true;
    },
    //保存修改密码
    saveNewPwd() {
      if (this.updatePasswordFormData) {
        let _this = this;
        changePwd(this.updatePasswordFormData).then((data) => {
          if (data && data.code === "200") {
            _this.exit();
          }
        });
      }
    },
    cancel(formName) {
      this.updatePasswordVisible = false;
      if (formName === "myInfo") this.$refs[formName].resetFields();
    },
  },
};
</script>

<style scoped>
.moduleWrapper {
  height: 100%;
}
.container-main {
  height: calc(100% - 60px);
}
.el-menu.el-menu--horizontal {
  display: flex;
  justify-content: flex-end;
}
.header {
  display: flex;
  padding: 0;
}
.main {
  padding: 0;
  height: calc(100%);
}
.title {
  width: 20%;
  color: yellow;
  text-align: center;
  line-height: 60px;
  /*font-size: 26px;*/
  font-size: 23px;
  background-color: rgb(207, 31, 0);
  display: flex;
  justify-content: space-evenly;
  cursor: pointer;
}
.topMenu .iconfont {
  font-size: 25px;
}
.navLeader {
  display: flex;
  height: 40px;
  line-height: 40px;
  font-size: 14px;
  color: #cf1f00;
  background: #eaeaea;
  border-bottom: 1px solid #e6a23c;
}
.navLeader h3 {
  flex: 1;
  padding-left: 15px;
}
.navLeader p {
  flex: 1;
  line-height: 50px;
  text-align: center;
  cursor: pointer;
}
.navLeader span {
  font-size: 24px;
}
.el-menu {
  border: none;
  height: 100%;
  width: 100% !important;
}
.tabColumn {
  height: 40px;
  /* overflow: hidden; */
  display: flex;
  display: -webkit-flex;
  border-bottom: 1px solid #e6a23c;
  background: #ecf0f5;
}
.tabColumn .el-tabs {
  height: 40px;
}
.navBar {
  flex: 0 0 40px;
  width: 40px;
}
.navBar .el-button {
  padding: 0;
  width: 40px;
  height: 40px;
  border-radius: 0;
}
.navBar .el-button:focus {
  border: none;
  border-right: 1px solid #bd392f;
  color: #bd392f;
}
.navBar .el-button:hover {
  border: none;
  border-right: 1px solid #bd392f;
  color: #bd392f;
}
.tabWrapper {
  flex: 1;
  display: flex;
}
.tabMain {
  flex: 1;
  max-width: 1156px;
  border-bottom: 1px solid #e6a23c;
}
.tabPrev,
.tabNext {
  flex: 0 0 40px;
  width: 40px;
  line-height: 40px;
  text-align: center;
  color: #999;
  cursor: pointer;
}
.tabPrev {
  border-right: 1px solid #e4e7ed;
}
.tabNext {
  border-left: 1px solid #e4e7ed;
}
.tabUsed {
  /* position: relative; */
  flex: 0 0 90px;
  width: 90px;
  /* z-index: 1; */
}
.tabUsed .el-button {
  width: 90px;
  height: 38px;
  line-height: 40px;
  border-radius: 0;
  border-top: 0;
  border-bottom: none;
  padding: 0;
  background: #fff;
  color: #303133;
  font-size: 13px;
  border: none;
  border-left: 1px solid #e4e7ed;
  border-right: 1px solid #e4e7ed;
}
.el-dropdown-menu li {
  font-size: 12px;
}
.tabScreen {
  flex: 0 0 40px;
  width: 40px;
  line-height: 40px;
  text-align: center;
  color: #999;
  cursor: pointer;
}
.tabColumn >>> .el-tabs--card > .el-tabs__header {
  border: none;
}
.tabColumn >>> .el-tabs--card > .el-tabs__header .el-tabs__nav {
  border: none;
}
.tabColumn >>> .el-tabs__item {
  border-bottom: none;
}
.tabColumn >>> .el-tabs__item.is-active.is-closable {
  background: #e6a23c;
  color: #fff;
}
.tabColumn >>> .el-tabs__nav-next,
.tabColumn >>> .el-tabs__nav-prev {
  width: 20px;
  height: 40px;
  line-height: 44px;
  text-align: center;
}
.tabColumn >>> .el-tabs__nav-next {
  border-left: 1px solid #e4e7ed;
}
.tabColumn >>> .el-tabs__nav-prev {
  border-right: 1px solid #e4e7ed;
}
.tabColumn >>> .el-tabs__nav-next i,
.tabColumn >>> .el-tabs__nav-prev i {
  font-size: 16px;
}
.noPadding {
  padding: 0;
  overflow: hidden;
  background: #ecf0f5;
}
.tabUsedDrop {
  position: absolute;
  left: 0px;
  top: 40px;
  padding: 0;
  margin: 0;
  list-style: none;
  border: solid 1px #ccc;
  min-width: 120px;
  padding: 5px 0;
  margin: 2px 0 0;
  border-radius: 4px;
  -webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  text-align: center;
  background: #fff;
}
.tabUsedDrop li {
  height: 40px;
  line-height: 40px;
  font-size: 12px;
  cursor: pointer;
  color: #777;
}
.appMain {
  height: calc(100% - 40px);
  padding: 0px;
}
.personal-center {
  padding-right: 12px;
}
.personal-center >>> .el-submenu__title {
  background: #bd1e02 !important;
}
.avatar {
  display: inline-block;
  border-radius: 50%;
  padding-right: 8px;
}
.avatar img {
  border-radius: 50%;
}
.nickName {
  font-size: 14px;
}
.leftMenu {
  position: relative;
  z-index: 1;
  -webkit-box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.1);
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.1);
}
.passWordDialog >>> .el-dialog {
  width: 25%;
}
.wlink {
  display: block;
  text-decoration: none;
  color: #fff;
}
.huiZhang {
  width: 50px;
  height: 50px;
  background: url(~@/assets/images/djtb.png) no-repeat;
  background-size: 50px 50px;
  margin-top: 1.5%;
}
</style>



